.cart-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.cart-container.embedded {
  max-width: 100%;
  margin: 0;
  padding: 0;
}

/* Better alignment when cart is embedded next to the menu */
.cart-container.embedded .cart-items {
  padding: 1rem;
}

.cart-container.embedded .cart-item {
  grid-template-columns: 80px 1fr;
  grid-template-rows: auto auto auto;
  gap: 0.5rem 0.75rem;
}

.cart-container.embedded .cart-item-image {
  width: 80px;
  height: 80px;
}

.cart-container.embedded .cart-item-details {
  grid-column: 2;
}

.cart-container.embedded .cart-item-controls {
  grid-column: 2;
  flex-wrap: wrap;
}

.cart-container.embedded .cart-item-total {
  grid-column: 2;
  justify-self: end;
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.cart-header h2 {
  font-size: 2rem;
  color: #333;
}

.clear-cart-btn {
  padding: 0.7rem 1.5rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s;
}

.clear-cart-btn:hover {
  background: #c82333;
}

.empty-cart {
  text-align: center;
  padding: 4rem 2rem;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.empty-cart h2 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 1rem;
}

.empty-cart p {
  color: #666;
  margin-bottom: 2rem;
}

.back-to-menu-btn {
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: opacity 0.3s;
}

.back-to-menu-btn:hover {
  opacity: 0.9;
}

.cart-items {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.cart-item {
  display: grid;
  grid-template-columns: 100px 1fr auto 120px;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid #eee;
}

.cart-item:last-child {
  border-bottom: none;
}

.cart-item-image {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  overflow: hidden;
}

.cart-item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cart-item-details h3 {
  font-size: 1.3rem;
  color: #333;
  margin-bottom: 0.3rem;
}

.cart-item-details p {
  color: #666;
  font-size: 0.9rem;
}

.cart-item-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.quantity-btn {
  width: 35px;
  height: 35px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background 0.3s;
}

.quantity-btn:hover {
  background: #f0f0f0;
}

.quantity {
  min-width: 30px;
  text-align: center;
  font-weight: bold;
}

.remove-btn {
  padding: 0.5rem 1rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  margin-left: 0.5rem;
}

.remove-btn:hover {
  background: #c82333;
}

.cart-item-total {
  font-size: 1.3rem;
  font-weight: bold;
  color: #667eea;
  text-align: right;
}

.cart-summary {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.summary-row {
  display: flex;
  justify-content: space-between;
  padding: 0.8rem 0;
  font-size: 1.1rem;
}

.summary-row.total {
  border-top: 2px solid #eee;
  margin-top: 0.5rem;
  padding-top: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  color: #667eea;
}

.cart-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.pay-now-btn,
.print-btn {
  padding: 1rem 2.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: opacity 0.3s;
}

.pay-now-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.print-btn {
  background: #28a745;
  color: white;
}

.pay-now-btn:hover,
.print-btn:hover {
  opacity: 0.9;
}

.qr-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

.qr-modal-content {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  text-align: center;
  max-width: 400px;
  width: 90%;
}

.qr-modal-content h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.qr-code-container {
  display: flex;
  justify-content: center;
  margin: 1.5rem 0;
  padding: 1rem;
  background: #f9f9f9;
  border-radius: 10px;
}

.qr-amount {
  font-size: 1.3rem;
  font-weight: bold;
  color: #667eea;
  margin: 1rem 0;
}

.qr-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.complete-payment-btn,
.cancel-btn {
  flex: 1;
  padding: 0.8rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: opacity 0.3s;
}

.complete-payment-btn {
  background: #28a745;
  color: white;
}

.cancel-btn {
  background: #6c757d;
  color: white;
}

.complete-payment-btn:hover,
.cancel-btn:hover {
  opacity: 0.9;
}

.print-bill {
  padding: 2rem;
  background: white;
}

.print-header {
  text-align: center;
  margin-bottom: 2rem;
  border-bottom: 2px solid #333;
  padding-bottom: 1rem;
}

.print-header h2 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.print-items {
  margin-bottom: 2rem;
}

.print-item {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  border-bottom: 1px solid #eee;
}

.print-total {
  text-align: right;
  font-size: 1.5rem;
  padding-top: 1rem;
  border-top: 2px solid #333;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .cart-container {
    margin: 1rem auto;
    padding: 0 0.5rem;
  }

  .cart-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .cart-header h2 {
    font-size: 1.5rem;
  }

  .cart-items {
    padding: 1rem;
  }

  .cart-item {
    grid-template-columns: 80px 1fr;
    grid-template-rows: auto auto;
    gap: 0.5rem;
  }

  .cart-item-image {
    width: 80px;
    height: 80px;
    grid-row: 1 / 3;
  }

  .cart-item-details {
    grid-column: 2;
  }

  .cart-item-controls {
    grid-column: 2;
    flex-wrap: wrap;
  }

  .cart-item-total {
    grid-column: 1 / -1;
    text-align: left;
    margin-top: 0.5rem;
  }

  .cart-actions {
    flex-direction: column;
  }

  .pay-now-btn,
  .print-btn {
    width: 100%;
  }

  .qr-modal-content {
    padding: 1.5rem;
  }

  .qr-actions {
    flex-direction: column;
  }
}

