.manage-menu-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.manage-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.manage-menu-header h2 {
  font-size: 2rem;
  color: #333;
}

.add-item-btn {
  padding: 0.8rem 1.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: opacity 0.3s;
}

.add-item-btn:hover:not(:disabled) {
  opacity: 0.9;
}

.add-item-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.menu-form {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.menu-form h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #333;
}

.form-group input {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s;
}

.form-group input:focus {
  outline: none;
  border-color: #667eea;
}

.form-group small {
  display: block;
  margin-top: 0.3rem;
  color: #666;
  font-size: 0.9rem;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.submit-btn,
.cancel-btn {
  flex: 1;
  padding: 0.8rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: opacity 0.3s;
}

.submit-btn {
  background: #28a745;
  color: white;
}

.cancel-btn {
  background: #6c757d;
  color: white;
}

.submit-btn:hover,
.cancel-btn:hover {
  opacity: 0.9;
}

.menu-items-list {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.menu-items-list h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.items-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
}

.manage-menu-item {
  background: #f9f9f9;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #eee;
}

.manage-menu-item-image {
  width: 100%;
  height: 150px;
  overflow: hidden;
}

.manage-menu-item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.manage-menu-item-info {
  padding: 1rem;
}

.manage-menu-item-info h4 {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 0.3rem;
}

.manage-menu-item-info p {
  font-size: 1.1rem;
  font-weight: bold;
  color: #667eea;
}

.manage-menu-item-actions {
  display: flex;
  gap: 0.5rem;
  padding: 1rem;
  border-top: 1px solid #eee;
}

.edit-btn,
.delete-btn {
  flex: 1;
  padding: 0.6rem;
  border: none;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: opacity 0.3s;
}

.edit-btn {
  background: #ffc107;
  color: #333;
}

.delete-btn {
  background: #dc3545;
  color: white;
}

.edit-btn:hover,
.delete-btn:hover {
  opacity: 0.9;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .manage-menu-container {
    margin: 1rem auto;
    padding: 0 0.5rem;
  }

  .manage-menu-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .manage-menu-header h2 {
    font-size: 1.5rem;
  }

  .add-item-btn {
    width: 100%;
  }

  .menu-form {
    padding: 1.5rem;
  }

  .menu-items-list {
    padding: 1.5rem;
  }

  .items-grid {
    grid-template-columns: 1fr;
  }
}







