.sales-report-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.sales-report-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.sales-report-header h2 {
  font-size: 2rem;
  color: #333;
}

.header-controls {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.month-selector {
  padding: 0.7rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
}

.export-btn {
  padding: 0.7rem 1.5rem;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: opacity 0.3s;
}

.export-btn:hover {
  opacity: 0.9;
}

.sales-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.stat-card h3 {
  font-size: 1rem;
  color: #666;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: bold;
  color: #667eea;
  margin: 0;
}

.sales-details {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.sales-details h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.no-sales {
  text-align: center;
  padding: 3rem;
  color: #666;
}

.sales-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.sale-item {
  display: grid;
  grid-template-columns: 150px 1fr auto;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
  background: #f9f9f9;
  border-radius: 10px;
  border-left: 4px solid #667eea;
}

.sale-date {
  display: flex;
  flex-direction: column;
}

.sale-date strong {
  font-size: 1rem;
  color: #333;
}

.sale-date span {
  font-size: 0.85rem;
  color: #666;
}

.sale-items {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.sale-item-name {
  background: white;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.9rem;
  color: #333;
}

.sale-total {
  font-size: 1.3rem;
  font-weight: bold;
  color: #667eea;
  text-align: right;
}

.daily-sales-chart {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.daily-sales-chart h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.chart-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.chart-bar {
  display: grid;
  grid-template-columns: 100px 1fr 100px;
  gap: 1rem;
  align-items: center;
}

.chart-label {
  font-size: 0.9rem;
  color: #666;
  font-weight: 500;
}

.chart-bar-container {
  background: #f0f0f0;
  height: 40px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}

.chart-bar-fill {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  height: 100%;
  border-radius: 20px;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  transition: width 0.3s;
  min-width: 50px;
}

.chart-bar-value {
  color: white;
  font-size: 0.9rem;
  font-weight: bold;
}

.chart-orders {
  font-size: 0.9rem;
  color: #666;
  text-align: right;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .sales-report-container {
    margin: 1rem auto;
    padding: 0 0.5rem;
  }

  .sales-report-header {
    flex-direction: column;
    align-items: stretch;
  }

  .sales-report-header h2 {
    font-size: 1.5rem;
  }

  .header-controls {
    flex-direction: column;
  }

  .month-selector,
  .export-btn {
    width: 100%;
  }

  .sales-stats {
    grid-template-columns: 1fr;
  }

  .stat-card {
    padding: 1.5rem;
  }

  .stat-value {
    font-size: 2rem;
  }

  .sales-details {
    padding: 1.5rem;
  }

  .sale-item {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }

  .sale-date {
    flex-direction: row;
    justify-content: space-between;
  }

  .sale-total {
    text-align: left;
  }

  .daily-sales-chart {
    padding: 1.5rem;
  }

  .chart-bar {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }

  .chart-label {
    font-size: 0.8rem;
  }

  .chart-orders {
    text-align: left;
    font-size: 0.8rem;
  }
}







