.menu-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.menu-container.embedded {
  max-width: 100%;
  margin: 0.5rem 0 1rem 0;
  padding: 0;
}

.menu-header {
  text-align: center;
  margin-bottom: 2rem;
}

.menu-header h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 0.5rem;
}

.menu-header p {
  color: #666;
  font-size: 1.1rem;
}

.menu-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1rem;
  padding: 0.5rem 0;
  align-items: stretch;
}

.menu-item {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.menu-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.menu-item-image {
  width: 100%;
  aspect-ratio: 4 / 3;
  overflow: hidden;
  background: #f0f0f0;
}

.menu-item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s;
}

.menu-item:hover .menu-item-image img {
  transform: scale(1.1);
}

.menu-item-info {
  padding: 1rem 1.2rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.menu-item-info h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 0.5rem;
}

.menu-price {
  font-size: 1.3rem;
  font-weight: bold;
  color: #667eea;
  margin: 0;
}

.add-to-cart-btn {
  width: 100%;
  padding: 0.75rem 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: opacity 0.2s ease;
  margin-top: auto;
}

.add-to-cart-btn:hover {
  opacity: 0.9;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .menu-container {
    margin: 1rem auto;
    padding: 0 0.5rem;
  }

  .menu-header h2 {
    font-size: 2rem;
  }

  .menu-grid {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    gap: 0.75rem;
  }

  .menu-item-image {
    aspect-ratio: 4 / 3;
  }

  .menu-item-info {
    padding: 0.8rem 1rem;
  }

  .menu-item-info h3 {
    font-size: 1.1rem;
  }

  .menu-price {
    font-size: 1rem;
  }
}

