.home-container {
  max-width: 1200px;
  margin: 1rem auto;
  padding: 0 1rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 1rem;
}

.home-column {
  min-width: 0;
}

.cart-column {
  position: sticky;
  top: 80px;
  align-self: start;
}

.menu-column, .cart-column {
  /* Ensure equal visual padding handled by grid gap */
}

@media (max-width: 1024px) {
  .home-container {
    grid-template-columns: 1.5fr 1fr;
  }
}

@media (max-width: 768px) {
  .home-container {
    grid-template-columns: 1fr;
    gap: 0.5rem;
    padding: 0 0.5rem;
  }
  .cart-column {
    position: static;
  }
}


